import React, { useState } from 'react';

import './Modal.scss';
import Backdrop from '../Backdrop/Backdrop';
import Button from '../Button/Button';
import InputField from '../InputField/InputField';

function Modal({ title, content, input, show, hide, getName, save, okayBtn, cancelBtn }) {
  const [ value, setValue ] = useState('');

  const handleSave = () => {
    if ( value.trim() !== '' ) save();
    hide();
  };

  const getValue = val => {
    setValue(val);
  };

  return(
    <div>
      <Backdrop show={show} hide={hide}/>
      {show && <div className='modal'>
        <h3>{title}</h3>
        <p>{content}</p>
        {
          input && <>
            <InputField type='text' placeholder='Palette Name' getValue={getValue} getName={getName}/>
            {value === '' && <p className='errorMsg'>( palette name is mandatory )</p>}
          </>
        }
        <div className='controlBtn'>
          {cancelBtn && <Button click={hide}>cancel</Button>}
          {okayBtn && <Button click={handleSave} primary>okay</Button>}
        </div>
      </div>}
    </div>
  );
};

export default Modal;