import React from 'react';
import './Palette.scss';
import { seedPalettes } from '../../utils/seedPalettes';
import ColorBox from '../../components/ColorBox/ColorBox';

function Palette({ match }) {
  const paletteId = match.params.paletteId;
  const palette = seedPalettes.find(palette => palette.id === paletteId);

  return(
    <div className='palette'>
      <div className='colorBoxes'>
        {
          palette.colors.map(color => (
            <ColorBox key={color.color} color={color.color}/>
          ))
        }
      </div>
    </div>
  );
};

export default Palette;