import React from 'react';
import './About.scss';

function About() {
  return(
    <div className='about'>
      <h2>About color picker</h2>
      <div className='wrapper'>
        <h4>What is Color Picker?</h4>
        <p>Color Picker is an open collection of color palettes,
           created by Naveen. Color Picker started as a personal
            small project built to share trendy color combinations
             between a group of designer friends. The collection scaled 
             up and now being used daily as a handy resources by thousands 
             of people all over the world. Color Picker was created with the 
             goal of celebrating the beauty of colors, and to serve as a 
             go-to resource for color inspiration.</p>
      </div>
      <div className='wrapper'>
        <h4>Who is it for?</h4>
        <p>Color Picker serves thousands of color schemes for many different 
          design and art purposes. The color palettes are being used by graphic
           designers, artists, illustrators, web developers, fashion designers,
            marketeers, interior designers, and more. People use Color Picker to get
             color inspiration and find the perfect palette for their projects. 
             Anyone can save their favorite palettes, manage their personal collection, 
             and have a quick access to copy the color codes.</p>
      </div>
      <div className='wrapper'>
        <h4>Who creates the color palettes?</h4>
        <p>You, the users, are the ones who create the palettes using Color Picker’s 
          palette creator. The collection is open, and everyone can create and submit
           their own color combination. That’s how we keep Color Picker diverse, colorful,
            social and inspiring. Each palette is a public property and not owned by a specific
             creator, nor by Color Picker.</p>
      </div>
    </div>
  );
};

export default About;