import * as actionType from './collectionActiontype';
import { addNewPalette, removePaletteFromList } from '../../utils/collection';

const syncLocalStorage = () => {
  return JSON.parse(window.localStorage.getItem('collection'));
};

const INITIAL_STATE = {
  palettes: syncLocalStorage() || []
};

const collectionReducer = ( state = INITIAL_STATE, action ) => {
  switch(action.type) {
    case actionType.ADD_PALETTE:
      return {
        ...state,
        palettes: addNewPalette(state.palettes, action.payload)
      }
    case actionType.REMOVE_PALETTE:
      return {
        ...state,
        palettes: removePaletteFromList(state.palettes, action.payload)
      }
    default:
      return state;
  }
};

export default collectionReducer;