import React, { useState } from 'react';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { IconContext } from 'react-icons';
import { FaTrash } from 'react-icons/fa';
import { CopyToClipboard } from 'react-copy-to-clipboard';

import './Collection.scss';
import { selectPalettes } from '../../redux/collection/collectionSelector';
import { removePalette } from '../../redux/collection/collectoinAction';
import Button from '../../components/Button/Button';

function Collection({ history, palettes, remove }) {
  const [ copied, setCopied ] = useState(false);

  const copy = () => {
    setCopied(true);
    setTimeout(() => {
      setCopied(false);
    }, 1600);
  };

  const paletteToRemove = ( e, paletteId ) => {
    e.stopPropagation();
    remove(paletteId);
  };

  return(
    <div className='collection'>
      <h2>My collection</h2>
      <p>All saved palettes</p>
      <div className='collection__container'>
        { palettes.length > 0 ? <div className='palettes'>
          {
            palettes.map(palette => (
              <div key={palette.id} className='palette'>
                <div className='colors'>
                  {
                    palette.colors.map(color => (
                      <div key={color.color} className='color' style={{background: `${color.color}`}}>
                        <CopyToClipboard text={`${color.color}`}>
                          <span className='colorCode' onClick={copy}>
                            {copied ? 'copied' : color.color}
                          </span>
                        </CopyToClipboard>
                      </div>
                    ))
                  }
                </div>
                <h4>{palette.paletteName}</h4>
                <IconContext.Provider value={{className: 'trashIcon'}}>
                  <FaTrash onClick={e => paletteToRemove(e, palette.id)}/>
                </IconContext.Provider>
              </div>
            ))
          }
        </div> : <div>
          <p className='message'>"make palettes and use in future projects"</p>
          <Button click={() => history.push('/generate')}>Go to create Palette</Button>
          </div>}
      </div>
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  palettes: selectPalettes
});

const mapDispatchToProps = dispatch => ({
  remove: paletteId => dispatch( removePalette(paletteId) )
});

export default connect(mapStateToProps, mapDispatchToProps)(Collection);