import React, { useState } from 'react';

import './ColorPicker.scss';
import { ReactComponent as AddIcon } from '../../assets/add.svg';
import { ReactComponent as RandomIcon } from '../../assets/random.svg';

function ColorPicker({ addNewColor, random }) {
  const [ color, setColor ] = useState('#0088cc');

  const handleColorChange = e => {
    setColor(e.target.value);
  };

  return(
    <div className='colorPicker'>
      <div className='color__picker_field'>
        <span className='color__code'>{color}</span>
        <input type='color' value={color} onChange={handleColorChange}/>
      </div>
      <div>
        <AddIcon className='pick__icon' onClick={() => addNewColor(color)} />
        <RandomIcon className='pick__icon' onClick={random} />
      </div>
    </div>
  );
};

export default ColorPicker;