import React from 'react';
import { FaTrash } from 'react-icons/fa';

import './NewColor.scss';

function NewColor({ color, remove }) {
  console.log('rendering');
  function deleteColor() {
    remove(color);
  }

  return(
    <div key={color} className='newColor' style={{background: `${color}`}}>
      <div className='info'>
        <p>{color}</p>
        <FaTrash className='trashIcon' onClick={deleteColor} />
      </div>
    </div>
  );
};

export default React.memo(NewColor);