import React from 'react';
import { withRouter } from 'react-router-dom';

import './MiniPalette.scss';
import MiniColorBox from '../MiniColorBox/MiniColorBox';

function MiniPalette({ history, id, paletteName, colors }) {
  return(
    <div className='miniPalette' onClick={() => history.push(`palettes/${id}`)}>
      <div className='miniColorBoxs'>
        {
          colors.map(color => (
            <MiniColorBox key={color.name} color={color.color}/>
          ))
        }
      </div>
      <div className='palette__name'>
        <h4>{paletteName}</h4>
      </div>
    </div>
  );
};

export default withRouter(MiniPalette);