import React from 'react';
import { Route, Redirect } from 'react-router-dom';

import bg from './assets/Confetti-Doodles.svg';

import Navbar from './containers/Navbar/Navbar';
import HomeScreen from './containers/HomeScreen/HomeScreen';
import Palettes from './containers/Palettes/Palettes';
import Palette from './containers/Palette/Palette';
import Generate from './containers/Generate/Generate';
import Collection from './containers/Collection/Collection';
import Terms from './containers/Terms/Terms';
import About from './containers/About/About';
import Footer from './containers/Footer/Footer';

function App() {
  return(
    <div style={{backgroundImage: `url(${bg})`}}>
      <Route component={Navbar}/>
      <Route path="/" exact component={HomeScreen}/>
      <Route path="/palettes" exact component={Palettes}/>
      <Route path="/generate" exact component={Generate}/>
      <Route path="/collection" exact component={Collection}/>
      <Route path="/about" exact component={About}/>
      <Route path="/terms-of-service" exact component={Terms}/>
      <Route path="/palettes/:paletteId" exact component={Palette}/>
      <Route component={Footer}/>
      <Redirect to="/"/>
    </div>
  );
};

export default App;