import React, { useState, useRef, useEffect } from 'react';
import { NavLink } from 'react-router-dom';

import './Navbar.scss';
import Menubar from '../../components/Menubar/Menubar';
import Backdrop from '../../components/Backdrop/Backdrop';
import { ReactComponent as Logo } from '../../assets/logo.svg';
import { ReactComponent as MenuIcon } from '../../assets/menu.svg';

function Navbar({ history, location }) {
  const [showModal, setShowModal] = useState(false);
  const navbarRef = useRef();

  useEffect(() => {
    navbarRef.current.scrollIntoView({behaviour: 'smooth'});
  }, [location.pathname])

  const toggleModal = () => {
    setShowModal(!showModal);
  };

  return(
    <div> 
      {
        showModal && 
        <>
          <Menubar close={toggleModal}/>
          <Backdrop show={toggleModal} hide={toggleModal}/>
        </>
      }
      <nav className='navbar' ref={navbarRef}>
        <div className='logo__title'>
          <Logo className='logo'/>
          <h3 onClick={() => history.push('/')}>Color Picker</h3>
        </div>
        <div className='nav__links'>
          <NavLink to='/collection' className='my__palette__link'>My Palettes</NavLink>
          <MenuIcon className='menu__icon' onClick={toggleModal}/>
        </div>
      </nav>
    </div>
  );
}

export default Navbar;