import React from 'react';
import { NavLink } from 'react-router-dom';

import './Menubar.scss';
import { ReactComponent as CancelIcon } from '../../assets/cancel.svg';

function Menubar({ close }) {
  return(
    <div className='menubar'>
      <div className='menubar__heading'>
        <h3>Color Picker</h3>
        <CancelIcon className='cancel__icon' onClick={close}/>
      </div>
      <NavLink to="/palettes" className='link' activeClassName='link--active ' onClick={close}>Explore Palette</NavLink>
      <NavLink to="/generate" className='link' activeClassName='link--active ' onClick={close}>Generate Palette</NavLink>
      <NavLink to="/collection" className='link' activeClassName='link--active ' onClick={close}>My Palette</NavLink>
      <NavLink to="/terms-of-service" className='link' activeClassName='link--active ' onClick={close}>Terms and Conditions</NavLink>
      <NavLink to="/about" className='link' activeClassName='link--active ' onClick={close}>About</NavLink>
    </div>
  );
}

export default Menubar;