import React from 'react';
import { withRouter } from 'react-router-dom';

import './CoverSection.scss';
import Button from '../Button/Button';
import { ReactComponent as ClipArtIcon } from '../../assets/paint-palette.svg';

function CoverSection({ history }) {
  return(
    <div className='cover__section'>
      <ClipArtIcon className='painter__palette'/>
      <div className='title'>
        <h2>Color Generator</h2>
        <p>Get inspired by beautiful color schemes, Create own palettes</p>
      </div>
      <div className='btns'>
        <Button primary click={() => history.push('/palettes')}>Explore Trending Palettes</Button>
        <Button click={() => history.push('/generate')}>Start Generating Palettes</Button>
      </div>
    </div>
  );
}

export default withRouter(CoverSection);