import React from 'react';
import './Footer.scss';
import { IconContext } from 'react-icons';
import { FaInstagram, FaFacebook, FaTwitter } from 'react-icons/fa';

function Footer({ history }) {
  return(
    <div className='footer'>
      <div className='footer__social__media'>
        <div>
          <h3 onClick={() => history.push('/')}>Color Picker</h3>
          <p>support us on social media</p>
        </div>
        <div className='socialLinks'>
          <IconContext.Provider value={{className: 'socialIcons'}}>
            <FaTwitter />
            <FaFacebook />
            <FaInstagram />
          </IconContext.Provider>
        </div>
      </div>
      <div className='copy__write'>
        <span>&#xA9; copywrite 2021 by naveen__comlay</span>
      </div>
    </div>
  );
};

export default Footer;
