import React from 'react';
import './Terms.scss';

function Terms() {
  return(
    <div className='terms'>
      <h2>Terms of services</h2>
      <div className='wrapper'>
        <h4>What the User should know at a glance</h4>
        <p>Please note that some provisions in these Terms
           may only apply to certain categories of Users. In 
           particular, certain provisions may only apply to Consumers 
           or to those Users that do not qualify as Consumers. Such 
           limitations are always explicitly mentioned within each affected 
           clause. In the absence of any such mention, clauses apply to all Users. <br /><br />
           Usage of Color Picker and the Service is age restricted, as detailed in the
            relevant section of this document.</p>
      </div>
      <div className='wrapper'>
        <h4>Terms of use</h4>
        <p>
          Unless otherwise specified, the terms of use detailed in this section
           apply generally when using Color Picker. Single or additional conditions of
            use or access may apply in specific scenarios and in such cases are
             additionally indicated within this document.<br /><br />
          By using Color Picker, Users confirm to meet the following requirements:
        </p>
        <ul className='list'>
          <li>There are no restrictions for Users in terms of being Consumers or Business Users.</li>
          <li>Users must be older than 13</li>
          <li>Users aren’t located in a country that is subject to a Indian Govt. embargo, 
          or that has been designated by the Indian Govt. as a “terrorist-supporting” country.</li>
          <li>Users aren’t listed on any Indian Govt. list of prohibited or restricted parties</li>
        </ul>
      </div>
    </div>
  );
};

export default Terms;