import React from 'react';
import './Palettes.scss';

import { seedPalettes } from '../../utils/seedPalettes';
import MiniPalette from '../../components/MiniPalette/MiniPalette';

function Palettes() {
  return(
    <div className='container'>
      <h2>Trending color palettes</h2>
      <p>Popular palette collections</p>
      <div className='popular__palettes'>
        {
          seedPalettes.map(palette => (
            <div key={palette.id} className='singlePalette'>
              <MiniPalette {...palette}/>
            </div>
          ))
        }
      </div>
    </div>
  );
};

export default Palettes;