import React, { useState } from 'react';
import { connect } from 'react-redux';

import './Generate.scss';
import Button from '../../components/Button/Button';
import NewColor from '../../components/NewColor/NewColor';
import ColorPicker from '../../components/ColorPicker/ColorPicker';
import Modal from '../../components/Modal/Modal';
import { seedPalettes } from '../../utils/seedPalettes';
import { addPalette } from '../../redux/collection/collectoinAction';

function Generate({ paletteToAdd }) {
  const [ colors, setcolors ] = useState([]);
  const [ showModal, setShowModal ] = useState(false);
  const [ paletteName, setPaletteName ] = useState('');
  const [ isCreated, setIsCreated ] = useState(false);

  const createBtnDisable = colors.length <= 0;

  const isPaletteCreated = () => {
    setIsCreated(!isCreated);
  };

  const toggleModal = () => {
    setShowModal(!showModal);
  };

  const hasColor = colorToAdd => {
    return colors.findIndex(color => color.color === colorToAdd.color) > -1;
  };

  const randomColor = () => {
    const allColors = seedPalettes.map(palette => palette.colors.map(color => color)).flat();
    const randomColor = allColors[Math.ceil(Math.random() * allColors.length)];
    const check = hasColor(randomColor);
    
    if (colors.length < 4 && !check) {
      setcolors([...colors, randomColor]);
    }
  };

  const addNewColor = color => {
    const newColor = { color };
    const check = hasColor(newColor);
    
    if (colors.length < 4 && !check) {
      setcolors([...colors, newColor]);
    }
  };

  const removeColor = id => {
    const updatedColors = colors.filter(color => color.color !== id);
    setcolors(updatedColors);
  };

  const getPaletteName = name => {
    setPaletteName(name);
  };

  const clearPalette = () => {
    setcolors([]);
  };

  const savePalette = () => {
    const id = paletteName.trim().toLocaleLowerCase().replace(/ /g, '-');

    const newPalette = { 
      id,
      paletteName, 
      colors 
    };

    paletteToAdd(newPalette);
    isPaletteCreated();
  };

  return(
    <div>
      {
        isCreated && <Modal title='Palette Created' content='Your palette successfully created' show={isCreated} save={isPaletteCreated} hide={isPaletteCreated} okayBtn/>
      }
      {showModal && 
        <Modal 
          title='Palette Name' 
          content='Make sure that palette name is unique!'
          input
          okayBtn
          cancelBtn
          show={showModal} 
          hide={toggleModal}
          getName={getPaletteName}
          save={savePalette}/>}
      <div className='generate'>
        <h2>Create palette</h2>
        <p>Start generating palettes</p>
        <div className='color__generator__container'>
          <ColorPicker addNewColor={addNewColor} random={randomColor} />
          {colors.length > 0 ? <div className='new__palette'>
            {
              colors.map(color => (
                <NewColor key={color.color} color={color.color} remove={removeColor}/>
              ))
            }
          </div> : <p className='message'>"Generate palette, 4 color per palette"</p>}
          <div className='createBtn'>
            <Button disabled={createBtnDisable} primary click={toggleModal}>Create Palette</Button>
            <Button click={clearPalette}>Clear All Color</Button>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapDispatchToProps = dispatch => ({
  paletteToAdd: palette => dispatch( addPalette(palette) )
});

export default connect(null, mapDispatchToProps)(Generate);