const syncLocalStorage = collection => {
  window.localStorage.setItem('collection', JSON.stringify(collection));
};

export const addNewPalette = ( currentState, paletteToAdd ) => {
  const updatedCollection = currentState.concat(paletteToAdd);
  syncLocalStorage(updatedCollection);
  return updatedCollection;
};

export const removePaletteFromList = ( currentState, paletteId ) => {
  const updatedCollection = currentState.filter(palette => palette.id !== paletteId);
  syncLocalStorage(updatedCollection);
  return updatedCollection;
};